            // var range = 500.0;
            // this.viewer.camera.lookAt(center, new Cesium.HeadingPitchRange(heading, pitch, range));
            // let jnTran2 = this.tranCoor(114.355002,30.564870) //东亭 江南114.371035,30.594435 
            // let jnTran2 = this.tranCoor(114.371035,30.594435) 
            // this.viewer.camera.flyTo({
            //     destination: Cesium.Cartesian3.fromDegrees(jnTran2[0], jnTran2[1], 100),
            //     orientation: {
            //         heading: Cesium.Math.toRadians(0), // 方向
            //         pitch: Cesium.Math.toRadians(-10.0),// 倾斜角度
            //         roll: 0
            //         // heading: 0.0,
            //         // // pitch: -Cesium.Math.PI_OVER_TWO,
            //         // pitch: -0.6,
            //         // roll: 0.0
            //     },
            //     duration: 3,
            //     complete: function () {
            //         // 到达位置后执行的回调函数
            //         console.log('到达目的地');
            //     },
            // });
            // // // this.getNewbodery();
            setTimeout(() => {
                var xxxx = this.viewer.dataSources;
                console.log(this.viewer.dataSources);
            }, 5000);
        },
        firstEnter2(lng, lat, height, pitch){
            let jnTran2 = this.tranCoor(parseFloat(lng), parseFloat(lat))
            // alert(jnTran2)
            this.viewer.camera.flyTo({
                destination: Cesium.Cartesian3.fromDegrees(jnTran2[0], jnTran2[1], height),
                orientation: {
                    // heading: Cesium.Math.toRadians(-20.0), // 方向
                    // pitch: Cesium.Math.toRadians(-10.0),// 倾斜角度
                    // roll: 0,
                    heading: 0.0,
                    // pitch: -Cesium.Math.PI_OVER_TWO,
                    pitch: pitch,
                    roll: 0.0
                },
                duration: 3
            });
            // this.getNewbodery();
            setTimeout(() => {
                var xxxx = this.viewer.dataSources;
                console.log(this.viewer.dataSources);
            }, 5000);
        },

        //获取坐标
        getAddress () {
            let _this = this
            var canvas = this.viewer.scene.canvas
            var ellipsoid = this.viewer.scene.globe.ellipsoid
            var handler = new Cesium.ScreenSpaceEventHandler(canvas)
            handler.setInputAction(function (movement) {
                // 捕获椭球体，将笛卡尔二维平面坐标转为椭球体的笛卡尔三维坐标，返回球体表面的点
                var cartesian = _this.viewer.camera.pickEllipsoid(movement.endPosition, ellipsoid)
                if (cartesian) {
                // 将笛卡尔三维坐标转为地图坐标（弧度）
                var cartographic = _this.viewer.scene.globe.ellipsoid.cartesianToCartographic(cartesian)
                // 将地图坐标（弧度）转为十进制的度数
                _this.lat = Cesium.Math.toDegrees(cartographic.latitude).toFixed(6)
                _this.lng = Cesium.Math.toDegrees(cartographic.longitude).toFixed(6)
                _this.height = (_this.viewer.camera.positionCartographic.height / 1000).toFixed(2)
                }
            }, Cesium.ScreenSpaceEventType.MOUSE_MOVE)
            handler.setInputAction(function (wheelment) {
                _this.height = Math.ceil(_this.viewer.camera.positionCartographic.height)
            }, Cesium.ScreenSpaceEventType.WHEEL)
        }

    },
    // beforeDestroy(){
    //     this.$store.commit('clearMap', 3)
    // }
}
</script>

