        // // pickId(){
        //     if(this.clickType){
        //         let pickId = this.pickId
        //         this.show = true
        //         console.log(pickId, 97)
        //         this.$store.commit('clickTypeFun', false)
        //         this.$store.commit('pickIdFun', '')
        //     } 
        // }
    },
    mounted(){
        Cesium.BingMapsApi.defaultKey = 'ArucOjRQCPXY7qrklhKLCkWG2M8JVDdSM8-1YcfjjJDDFHpNqwCiiTXo76-Sgjy4';
        let viewer = new Cesium.Viewer('cesiumContainer', //创建地图实例
        {
            animation: false,
            baseLayerPicker: false,
            fullscreenButton: true,
            geocoder: false,
            timeline: false,
            navigationHelpButton: false,
            homeButton: true,
            infoBox: false,
            sceneModePicker: false,
            scene3DOnly: true,
            selectionIndicator: false, 
            imageryProvider: new Cesium.ArcGisMapServerImageryProvider({
                url:"http://services.arcgisonline.com/ArcGIS/rest/services/World_Street_Map/MapServer",
                layer: "tdtBasicLayer",
                style: "default",
                format: "image/jpeg",
                tileMatrixSetID: "GoogleMapsCompatible",
                show: false,
                maximumLevel: 18
            })
            // imageryProvider: new Cesium.WebMapTileServiceImageryProvider({
            // url:
            // // "http://t1.tianditu.com/vec_c/wmts?layer=vec&style=default&tilematrixset=c&Service=WMTS&Request=GetTile&Version=1.0.0&Format=tiles&TileMatrix={z}&TileCol={x}&TileRow={y}&tk=35202223aa9de072aaf6c35fb5a51f59",
            // "http://t0.tianditu.com/vec_w/wmts?service=wmts&request=GetTile&version=1.0.0&LAYER=vec&tileMatrixSet=w&TileMatrix={TileMatrix}&TileRow={TileRow}&TileCol={TileCol}&style=default&format=tiles&tk=35202223aa9de072aaf6c35fb5a51f59",
            // layer: "tdtVecBasicLayer",
            // style: "default",
            // format: "image/jpeg",
            // tileMatrixSetID: "GoogleMapsCompatible",
            // maximumLevel: 18,
            // show: false
            // })
        });
        this.viewer = viewer
        this.viewer._cesiumWidget._creditContainer.style.display = "none";
            // 矢量中文标注
        // viewer.imageryLayers.addImageryProvider(
        //     new Cesium.WebMapTileServiceImageryProvider({
        //     url:
        //     "http://t0.tianditu.com/cva_w/wmts?service=wmts&request=GetTile&version=1.0.0&LAYER=cva&tileMatrixSet=w&TileMatrix={TileMatrix}&TileRow={TileRow}&TileCol={TileCol}&style=default.jpg&tk=35202223aa9de072aaf6c35fb5a51f59",
        //     layer: "tdtAnnoLayer",
        //     style: "default",
        //     format: "image/jpeg",
        //     // maximumLevel: 18,
        //     tileMatrixSetID: "GoogleMapsCompatible"
        //     })
        // );
        //去虚化
        viewer.scene.postProcessStages.fxaa.enabled = false;
        /**
        * 设置后当相机高度达到设置的最大和最小高度时将不再放大和缩小
        */
		// viewer.scene.screenSpaceCameraController.minimumZoomDistance = 250000;//相机的高度的最小值
		// viewer.scene.screenSpaceCameraController.maximumZoomDistance = 22000000;  //相机高度的最大值
		// viewer.scene.screenSpaceCameraController._minimumZoomRate = 30000; // 设置相机缩小时的速率
		// viewer.scene.screenSpaceCameraController._maximumZoomRate=5906376272000    //设置相机放大时的速率

        
        /*定义初始缩放，值可以改动，以下括号里的值可以看情况自己设置。*/
        // viewer.camera.zoomIn(0);
        // /*放大*/
        // viewer.camera.zoomIn(1000);
        // /*缩小*/  
        // viewer.camera.zoomOut(1000);
        // /*左旋转*/    
        // viewer.camera.rotateLeft(Cesium.Math.toDegrees(0.005).toFixed(2));
        // /*viewer.camera.rotateLeft(0.000001);*/
        // /*右旋转*/       
        // viewer.camera.rotateRight(Cesium.Math.toDegrees(0.005).toFixed(2));
        // /*viewer.camera.rotateRight(0.000001);*/
        // /*上倾斜，偏左旋转*/ 
        // viewer.camera.twistRight(Cesium.Math.toDegrees(0.005).toFixed(2));
        // /*下倾斜，偏右旋转*/ 
        // viewer.camera.twistLeft(Cesium.Math.toDegrees(0.005).toFixed(2)); 


        var options = {};
        // 用于在使用重置导航重置地图视图时设置默认视图控制。接受的值是Cesium.Cartographic 和 Cesium.Rectangle.
        options.defaultResetView = Cesium.Rectangle.fromDegrees(80, 22, 130, 50);
        // 用于启用或禁用罗盘。true是启用罗盘，false是禁用罗盘。默认值为true。如果将选项设置为false，则罗盘将不会添加到地图中。
        options.enableCompass= true;
        // 用于启用或禁用缩放控件。true是启用，false是禁用。默认值为true。如果将选项设置为false，则缩放控件将不会添加到地图中。
        options.enableZoomControls= true;
        // 用于启用或禁用距离图例。true是启用，false是禁用。默认值为true。如果将选项设置为false，距离图例将不会添加到地图中。
        options.enableDistanceLegend= true;
        // 用于启用或禁用指南针外环。true是启用，false是禁用。默认值为true。如果将选项设置为false，则该环将可见但无效。
        options.enableCompassOuterRing= true;
