        getAddress2 () {
            let _this = this
            var canvas = this.viewer.scene.canvas
            var ellipsoid = this.viewer.scene.globe.ellipsoid
            var handler = new Cesium.ScreenSpaceEventHandler(canvas)
            handler.setInputAction(function (movement) {
                // 捕获椭球体，将笛卡尔二维平面坐标转为椭球体的笛卡尔三维坐标，返回球体表面的点
                var cartesian = _this.viewer.camera.pickEllipsoid(movement.endPosition, ellipsoid)
                if (cartesian) {
                // 将笛卡尔三维坐标转为地图坐标（弧度）
                var cartographic = _this.viewer.scene.globe.ellipsoid.cartesianToCartographic(cartesian)
                // 将地图坐标（弧度）转为十进制的度数
                _this.lat = Cesium.Math.toDegrees(cartographic.latitude).toFixed(6)
                _this.lng = Cesium.Math.toDegrees(cartographic.longitude).toFixed(6)
                alert(_this.lat)
                _this.height = (_this.viewer.camera.positionCartographic.height / 1000).toFixed(2)
                }
            }, Cesium.ScreenSpaceEventType.LEFT_CLICK)
            // handler.setInputAction(function (wheelment) {
            //     _this.height = Math.ceil(_this.viewer.camera.positionCartographic.height)
            // }, Cesium.ScreenSpaceEventType.WHEEL)
        },

        getPosition() {
            let _this = this
            //得到当前三维场景
            var scene = _this.viewer.scene;
            //得到当前三维场景的椭球体
            var ellipsoid = scene.globe.ellipsoid;
            var entity = _this.viewer.entities.add({
                label : {
                    show : false
                }
            });
            var longitudeString = null;
            var latitudeString = null;
            var height = null;
            var cartesian = null;
            // 定义当前场景的画布元素的事件处理
            var handler = new Cesium.ScreenSpaceEventHandler(scene.canvas);
            //设置鼠标移动事件的处理函数，这里负责监听x,y坐标值变化
            handler.setInputAction(function(movement) {
                //通过指定的椭球或者地图对应的坐标系，将鼠标的二维坐标转换为对应椭球体三维坐标
                cartesian = _this.viewer.camera.pickEllipsoid(movement.endPosition, ellipsoid);
                if (cartesian) {
                    //将笛卡尔坐标转换为地理坐标
                    var cartographic = ellipsoid.cartesianToCartographic(cartesian);
                    //将弧度转为度的十进制度表示
                    longitudeString = Cesium.Math.toDegrees(cartographic.longitude);
                    latitudeString = Cesium.Math.toDegrees(cartographic.latitude);
                    //获取相机高度
                    height = Math.ceil(_this.viewer.camera.positionCartographic.height);
                    entity.position = cartesian;
                    entity.label.show = true;
                    entity.label.text = '(' + longitudeString + ', ' + latitudeString + "," + height + ')' ;
                }else {
                    entity.label.show = false;
                }
            }, Cesium.ScreenSpaceEventType.MOUSE_MOVE);
            //设置鼠标滚动事件的处理函数，这里负责监听高度值变化
            handler.setInputAction(function(wheelment) {
                height = Math.ceil(_this.viewer.camera.positionCartographic.height);
                entity.position = cartesian;
                entity.label.show = true;
                entity.label.text = '(' + longitudeString + ', ' + latitudeString + "," + height + ')' ;
            }, Cesium.ScreenSpaceEventType.WHEEL);
        },

        getpoint(){
            let _this = this;
            // var viewer = new Cesium.Viewer('container');
            var handler = new Cesium.ScreenSpaceEventHandler(_this.viewer.scene.canvas);
            handler.setInputAction(function (event) {
                // 屏幕坐标转世界坐标——关键点
                var ellipsoid = _this.viewer.scene.globe.ellipsoid;
                var cartesian = _this.viewer.camera.pickEllipsoid(event.position, ellipsoid);
                //将笛卡尔坐标转换为地理坐标
                var cartographic = Cesium.Cartographic.fromCartesian(cartesian);
                //将弧度转为度的十进制度表示
                var lon = Cesium.Math.toDegrees(cartographic.longitude);
                var lat = Cesium.Math.toDegrees(cartographic.latitude);
                alert(lon+','+lat)
            }, Cesium.ScreenSpaceEventType.LEFT_CLICK)

        },

        //获取坐标
        getAddress () {
            let _this = this
            var canvas = this.viewer.scene.canvas
            var ellipsoid = this.viewer.scene.globe.ellipsoid
            var handler = new Cesium.ScreenSpaceEventHandler(canvas)
            handler.setInputAction(function (movement) {
                // 捕获椭球体，将笛卡尔二维平面坐标转为椭球体的笛卡尔三维坐标，返回球体表面的点
                var cartesian = _this.viewer.camera.pickEllipsoid(movement.endPosition, ellipsoid)
                if (cartesian) {
                // 将笛卡尔三维坐标转为地图坐标（弧度）
                var cartographic = _this.viewer.scene.globe.ellipsoid.cartesianToCartographic(cartesian)
                // 将地图坐标（弧度）转为十进制的度数
                _this.lat = Cesium.Math.toDegrees(cartographic.latitude).toFixed(6)
                _this.lng = Cesium.Math.toDegrees(cartographic.longitude).toFixed(6)
                _this.height = (_this.viewer.camera.positionCartographic.height / 1000).toFixed(2)
                }
            }, Cesium.ScreenSpaceEventType.MOUSE_MOVE)
            handler.setInputAction(function (wheelment) {
                _this.height = Math.ceil(_this.viewer.camera.positionCartographic.height)
            }, Cesium.ScreenSpaceEventType.WHEEL)
        },
        //聚焦设置颜色
        setModelColor(each, color) {
        if (each.type == 5 || each.type == 4) {
            this.viewer.entities.getById(each.id).polygon.material = color;
        }
        // else if (each.shapeType == 2) {
        //   this.viewer.entities.getById(each.id).rectangle.material = color;
        // }
        },
        //世界坐标转地理
        world_to_WGS84(point) {
        var ellipsoid = this.viewer.scene.globe.ellipsoid;

        let cartesian = this.viewer.camera.pickEllipsoid(point, ellipsoid);

        var cartographic = this.viewer.scene.globe.ellipsoid.cartesianToCartographic(
            cartesian
        );

        var lat = Cesium.Math.toDegrees(cartographic.latitude);

        var lng = Cesium.Math.toDegrees(cartographic.longitude);

        var alt = cartographic.height;

        return {
            lat,
            lng,
            alt
        };
        }

    },
    components: {
        addHouspane,
        addEquipment
    }
    // beforeDestroy(){
    //     this.$store.commit('clearMap', 3)
    // }
}
</script>

