<template>
    <div class="openequipment">
        <h3 class="tableTitle">
            <!-- <i class="el-icon-close" @click="sendMsgToParent"></i>  -->
            添加设备
        </h3>
        <div class="tabOpener">
            <el-form :label-position="labelPosition" label-width="80px">
                <el-row>
                </el-row>
            </el-form>

            <div class="table-con">
                <el-form>
                    <el-row :gutter="10">
                        <el-col :span="24">
                            <el-form-item label-width="90px" label="设备类型:">
                                <el-select v-model="value" placeholder="请选择">
                                    <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="24">
                            <el-form-item label-width="90px" label="设备编号:">
                                <el-input v-model="listQuery.houseH"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="24">
                            <el-form-item label-width="90px" label="设备地址:">
                                <el-input v-model="listQuery.houseH"></el-input>
                            </el-form-item>
                        </el-col>
                        
                    </el-row>
                </el-form>
            </div>

            <el-row>
                <el-col :span="24" align="center">
                    <el-button type="primary" @click="updateHouse">保存</el-button>
                    <el-button @click="sedMsgload">取消</el-button>
                </el-col>
            </el-row>
        </div>
        <div class="mj-photo">
            <i class="el-icon-close" @click="closePhoto"></i>
            <img :src="photoUrl">
        </div>
    </div>
</template>
<script>
// import { getDoorRecord } from '@/api/homePage';
// import { formatDate } from '@/assets/js/utils'

// const yesterday = formatDate(new Date().getTime() - 24 * 60 * 60 * 1000);
// const today = formatDate(new Date().getTime());
export default {
    name: 'mjTabPane',
    props: ['community', 'messageHouseNum'],
    data() {
        return {
            list: null,
            total: null,
            listLoading:false,
            prefix: this.$store.state.prefix,
            photoUrl: 'https://sc-wc-hjhy.oss-cn-hangzhou.aliyuncs.com/10032/open_door_face/20181031/1540975605_zhlps4gt.jpeg',
            listQuery: {
                deviceNo: '',
                beginTime: '',
                endTime: '',
                isKey: '',
                pageNum: 1,
                pageSize: 10,
                houseH:'',
                communityId: this.community
            },
            labelPosition: 'right',
            options: [{
                value: '选项1',
                label: '大门门禁'
                }, {
                value: '选项2',
                label: '单元门禁'
                }, {
                value: '选项3',
                label: '人脸卡口'
                }, {
                value: '选项4',
                label: '车辆道闸'
                }, {
                value: '选项5',
                label: '周界预警'
                }],
            value: ''
        };
    },
    mounted() {
        // this.getList();
    },
    methods: {
        updateHouse() {
            alert("暂无数据接口，无法保存！")
        },
        sedMsgload(){
            this.$emit("loadHouse","click")
        },
        getList(deviceNo) {
            this.listLoading = true;
            if(deviceNo){
                this.listQuery.deviceNo = deviceNo;
                $('.mj-photo').hide();
            }
            getDoorRecord(this.listQuery).then(response => {
                this.list = response.data.data.list;
                this.total = response.data.data.total;
                this.listLoading = false;
                // this.list = response.data.list
                // this.total = response.data.total
            });
        },
        handleCurrentChange(val) {
            this.listQuery.pageNum = val;
            this.getList();
        },
        handleFilter() {
            this.listQuery.pageNum = 1;
            this.getList();
        },
        sendMsgToParent() {
            $('.openequipment').animate({ right: '-750' }, 500);
            $('.mj-photo').hide();
        },
        rowClick(row){
            if(row.photoNo){
                this.photoUrl = this.prefix + row.photoNo;
                $('.mj-photo').show();
            }else{
                this.$notify({
                    message: '暂时没有图片',
                    title:'温馨提示',
                    type:'warning'
                });
            }
        },
        closePhoto(){
            $('.mj-photo').hide();
        }
    }
};
</script>

<style lang="scss" scoped>
.openequipment {
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
    width: 550px;
    height: 530px;
    position: fixed;
    right: -750px;
    z-index: 1000;
    top: 50%;
    transform: translateY(-45%);
    border: 1px solid #00c0ff;
    border-radius: 5px;
    background-image: -webkit-linear-gradient(-90deg,rgba(0, 59, 113, 0.99) 0%,rgba(0, 36, 102, 0.92) 100%);
    background-image: -o-linear-gradient(-90deg,rgba(0, 59, 113, 0.99) 0%,rgba(0, 36, 102, 0.92) 100%);
    background-image: -moz-linear-gradient(-90deg,rgba(0, 59, 113, 0.99) 0%,rgba(0, 36, 102, 0.92) 100%);
    background-image: linear-gradient(-90deg,rgba(0, 59, 113, 0.99) 0%,rgba(0, 36, 102, 0.92) 100%);

}
.tableTitle {
    margin: 0 0 10px 0;
    padding: 0 20px;
    text-align: left;
    color: #00f8ff;
    line-height: 80px;
    font-size: 30px;
    height: 80px;
    border-bottom: 1px solid #00c0ff;
    //   background: url("../assets/images/tableTitleBg.png") left no-repeat;
    i {
        float: right;
        cursor: pointer;
    }
}
.tabOpener {
    width: 550px;
    height: 445px;
    //   background: rgba(8, 26, 59, 0.9);
    padding: 10px 10px;
    color: #f8f8f8;
    text-align: left;
    box-sizing: border-box;
}
.table-con {
    box-sizing: border-box;
    -moz-box-sizing: border-box; /* Firefox */
    -webkit-box-sizing: border-box; /* Safari */
    height: 250px;
    // background: rgba(0, 0, 0, 0.1);
    // border: 1px solid #00c0ff;
    border-radius: 4px;
    margin: 0 0 20px 0;
    padding: 10px 10px 10px 10px;
}
.imgCont {
    height: 100%;
}
.f_right {
    text-align: right;
}
.pubBtn {
    width: 80px;
    height: 36px;
    background-image: linear-gradient(-180deg, #216bff 0%, #21ccfe 100%);
    border-radius: 4px;
}
.el-icon-close {
    line-height: 80px;
}
.mj-photo{
    position:absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    width: 600px;
    background: rgba(8,26,59,0.90);
    padding: 20px;
    border: 1px solid #2463d5;
    border-radius: 4px;
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
    display: none;
    i{
        position: absolute;
        right: 5px;
        top: 5px;
        color: #fff;
        cursor: pointer;
    }
    img{
        width: 100%;
    }
}
.mj-photo .el-icon-close{
    line-height: 24px;
}
</style>



